@import "../../Shared/SASS/variables";

.projectSection {
  &__background {
    &--light {
      background-color: $pure-white;
    }
    &--dark {
      background-color: $cyprus;
    }
  }

  &__title {
    font-size: 5rem;
    padding-top: 5rem;
    font-family: "Raleway", sans-serif;
    font-weight: bold;
    text-align: center;

    &--light {
      color: $neon-blue;
    }
    &--dark {
      color: $deep-sky-blue;
    }
  }
}

@media screen and (max-width: 480px) {
  .projectSection__title {
    padding-top: 2.5rem;
    font-size: 3rem;
  }
}
